code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
  margin: 0;
  font-family: 'Noto Sans TC', sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}




#spin, #reset {
  position: absolute;
  bottom: 20vh;
  right: 45vw;
}

#spin:hover {
  color: rgb(0, 180, 120);
  border-color: rgb(0, 180, 120);
}

#spin:focus, #reset:focus {
  outline:none;
}

/* #reset {
  position: absolute;
  bottom: 20vh;
  right: 35vw;
} */

#reset:hover {
  color: red;
  border-color: red;
}



#readout {
  position: absolute;
  bottom: 7vh;
  left: 35vw;
}


#selector {
  position: absolute;
  top: 21vh;
  right: 47.5vw;
  font-size: 40px;
  z-index: 3;
}

#result{
  font-size:50px;
  color: rgb(0, 180, 120);
}

input {
  font-size: 16px;
  border:none;
  border-bottom: 1px solid black
}
.App {
    font-family: sans-serif;
    text-align: center;
  }

  br {
    display: block;
    margin: 10px 0;
    line-height: 50%;
  }
 


.wrapper {
    display: grid;
    grid-template-columns:0.1fr 3fr 1fr 1fr 0.2fr;
    grid-template-rows: 0.1fr 0.1fr 0.1fr 0.3fr 0.2fr 0.1fr;
    grid-gap: 1px;
}

.goalImage {
    grid-column: 3 / 5;
    grid-row: 2 ;
    margin-left: auto;
    margin-right: auto;

}

.title{
    grid-column: 3;
    grid-row: 2 ;
}

.subtitle{
    grid-column: 3 / 5;
    grid-row: 3 / 5 ;
    margin-top: 10%;
    margin-left: 11%;
    margin-right: 11%;
    text-align: center;
}

.middleText{
    grid-column: 2;
    grid-row: 3;
    margin-top: 18%;
    margin-left: 33%;
}

/* .middleText{
    grid-column: 2;
    grid-row: 3;
    margin-top: 25%;
    margin-left: 40%;
} */

.mainViz{
    grid-column: 2;
    grid-row: 2 / 4 ;
    margin-right: 3%;
    margin-top: 20px;
    margin-bottom: auto;

}

.mainVizOpa{
    grid-column: 2;
    grid-row: 2 / 4 ;
    margin-top: 33%;

    margin-left: 30%;
    z-index: 20;
    opacity : 0;
 
}

.needle{
    grid-column: 2;
    grid-row: 3;
    
    margin-left: auto;
    margin-right: 1em;
    margin-top: 10em;
    /* margin-bottom: 10px; */
}

.middleBar{
    grid-column: 2;
    grid-row: 3;
    
    margin-left: 33%;
    margin-top: 33%;
    margin-bottom: auto;
}

.mapImage{
    
    grid-column: 3 / 5;
    grid-row: 4 ;
    margin-right: 10%;
    margin-top: 10%;
    display: inline
}


.mapInfo{
    
    grid-column: 3 / 5;
    grid-row: 4 ;
    margin-top: 10%;
    
    margin-left: 50%;
    z-index: 3;
    display: inline
    
} 





.flashCards {
    grid-column: 3;
    grid-row: 5;
    margin-top: 10%;
}

.flashCardsBottom {
    grid-column: 4;
    grid-row: 5;
    margin-top: 10%;
}

